.clientForm {
  display: flex;
  width: 800px;
  border-radius: 5px;
  padding: 0px;
  box-shadow: 1px 1px 30px 5px #02022717;
  height: 450px;
  @media screen and ( max-width: 840px ) {
    flex-direction: column;
    width: 100%;
    height: 600px;
    align-items: center; }
  @media screen and ( max-width: 620px ) {
    width: 100%; }
  &-order {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
    background: linear-gradient( rgba(26, 24, 23, 0.95), rgba(22, 24, 23, 0.95) ), url('./background.jpg') center (center / cover) no-repeat;
    padding: 20px;
    padding-left: 80px;
    border-radius: 5px 0 0 5px;
    @media screen and ( max-width: 840px ) {
      width: 100%;
      border-radius: 5px 5px 0 0;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      padding: 20px;
      @media screen and ( max-width: 360px ) {
        padding: 10px; } } }
  &-form {
    width: 50%;
    position: relative;
    @media screen and ( max-width: 840px ) {
      width: 70%;
      @media screen and ( max-width: 440px ) {
        width: 100%; } }
    &--date {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top:  50px;
      &>div.check-personal-data {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        margin-top: -20px;
        z-index: 3;
        &>span {
          margin-left: 5px;
          font-size: 10px; } }
      &>span {
        font-family: Merriweather;
        font-size: 18px;
        margin-bottom: 50px;
        text-align: center; }
      &_buttons {
        &--prime {
          transition: all .5s;
          cursor: pointer;
          background: #258D00;
          border: none;
          box-shadow: 2px 2px 2px rgba(3, 169, 244, 0.2);
          border-radius: 5px;
          height: 40px;
          width: 130px;
          color: white;
          &:hover {
            background: darken(#258D00, 15); } }
        &--second {
          transition:  all .5s;
          cursor: pointer;
          background: transparent;
          border: 1px solid gray;
          box-shadow: 2px 2px 2px rgba(3, 169, 244, 0.2);
          border-radius: 5px;
          height: 40px;
          width: 130px;
          color: lighten(black, 10);
          margin-right: 20px;
          &:hover {
            color: black;
            border: 1px solid black; } } }
      &_close {
        width: 35px;
        height: 35px;
        position: absolute;
        top: 10px;
        right: 25px;
        cursor: pointer;
        background-color: white;
        border: none;
        z-index: 2;
        @media screen and ( max-width: 840px ) {
          right: -40px;
          @media screen and ( max-width: 640px ) {
            right: 0;
            @media screen and ( max-width: 440px ) {
              right: 40px; } } }
        &:hover>div {
          background: black; }
        &>div {
          width: 1px;
          height: 25px;
          background: gray;
          transition: .5s all;
          &:hover {
            background: black; }
          &:first-child {
            transform: rotate(45deg) translate(18px, -4px); }
          &:last-child {
            transform: rotate(-45deg) translate(22px, 0px); } } } } }
  &-item {
    display: flex;
    align-items: center;
    margin-bottom: 50px;
    @media screen and ( max-width: 840px ) {
      width: 50%;
      margin-bottom: 30px;
      height: 40px;
      &:nth-child(3) {
        margin-bottom: 0;
        @media screen and ( max-width: 360px ) {
          width: 55%; } }
      &:nth-child(2) {
        width: 40%; }
      &:nth-child(4) {
        width: 40%; } }
    &:last-child {
      margin-bottom: 0; }
    &--data {
      margin-left: 20px;
      display: flex;
      flex-direction: column;
      @media screen and ( max-width: 360px ) {
        margin-left: 10px; }
      &_header {
        font-family: Merriweather;
        font-size: 16px;
        color: white; }
      &_description {
        margin-top: 4px;
        font-family: Roboto;
        font-size: 12px;
        color: white;
        font-weight: 300; } } } }

.rc-tabs {
  border: none; }

.rc-tabs-bar {
  display: none; }

