.driverpage {
  padding-bottom: 100px;
  padding-top: 100px;
  margin: auto;
  width: 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-image: linear-gradient(to right, rgba(39,38,36,.85), rgba(39,38,36,.9)), url('../../pages/driverPage/Rectangle.jpg');
  &>h2 {
    font-family: Merriweather;
    font-weight: bold;
    font-size: 42px;
    text-align: center;
    color: #FFFFFF; }
  &>h3 {
    font-family: Merriweather;
    font-weight: bold;
    font-size: 32px;
    text-align: center;
    color: #FFFFFF;
    margin-top: 28px; } }

.driver_data {
  display: flex;
  justify-content: space-between;
  padding: 30px;
  margin: 0 auto;
  margin-top: 40px;
  min-height: 500px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.25);
  width: 90%;
  @media screen and ( max-width: 1024px ) {
    flex-direction: column;
    align-items: center; }
  &_photos {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-wrap: wrap;
    min-height: 400px;
    @media screen and ( max-width: 1024px ) {
      width: 75%; }
    @media screen and ( max-width: 780px ) {
      width: 100%; }
    &_item {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 48%;
      height: 200px;
      border-radius: 3px;
      @media screen and ( max-width: 1024px ) {
        height: 190px; } }
    &_line {
      display: flex;
      height: 50%;
      width: 100%;
      justify-content: space-between; }
    &-item {
      width: 45%; } }
  &_content {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 45%;
    border-left: 1px solid #e7e7e7;
    @media screen and ( max-width: 1024px ) {
      margin-top: 60px;
      width: 75%;
      border-left: 1px solid #e9e9e998;
      border-right: 1px solid #e9e9e998; }
    @media screen and ( max-width: 780px ) {
      width: 100%; }
    &_title {
      &_name {
        font-weight: 400;
        font-size: 24px;
        color: #000000; }
      &_stars {
        margin-top: 5px; }
      &_reviews {
        margin-top: 5px;
        font-size: 12px;
        color: #504743c5; } }
    &_car {
      margin-top: 40px; }
    &_btn {
      background-color: transparent;
      margin-top: 30px;
      box-shadow: none; } }
  &_list {
    &>li {
      margin-top: 5px; }
    &-title {
      font-size: 16px;
      display: inline-block;
      width: 120px;
      font-weight: 400; }
    &-description {
      font-size: 14px;
      font-weight: 300; } } }

.pplreviews {
  min-height: 100px;
  width: 100%;
  &_container {
    display: block;
    margin: 0 auto;
    margin-top: 30px;
    padding: 30px;
    min-height: 100px;
    width: 90%;
    box-shadow: 2px 4px 9px rgba(0, 0, 0, 0.25);
    background-color: white;
    border-radius: 15px; }
  &_data {
    display: flex;
    &_photo {
      max-width: 50px;
      max-height: 50px;
      border-radius: 100%;
      &>img {
        width: 100%;
        height: 100%; } }
    &_info {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      &-title {
        display: flex;
        align-items: flex-end; }
      &-ratting {
        margin-top: 5px; } }
    &_name {
      font-family: Merriweather;
      font-weight: 500;
      font-size: 12px; }
    &_date {
      margin-left: 10px;
      font-size: 12px;
      line-height: 14px;
      color: #C1C1C1; } }
  &_text {
    margin-top: 0px;
    font-size: 13px;
    line-height: 160%; } }

.driverpage {
  .driver {
    &_info {
      width: 100%;
      max-width: 220px;
      @media screen and ( max-width: 780px ) {
        margin-top: 0; }
      &_driver {
        &:hover {
          &>.driver_info_driver-data>div>.driver_info_driver-data_name {
            color: black; } }
        &>figure {}
        &-photo {}
        &-data {
          margin-left: 0;
          &_name {}
          &_rating {}
          &_reviews {
            font-size: 14px; } } }
      &_car {
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        @media screen and ( max-width: 400px ) {
          align-items: center; }
        &_name {
          margin-top: 40px; }
        &-text {
          &_title {
            width: 150px; }
          &_description {} }
        &-button {
          color: $global_secondary_color;
          border: $global_secondary_color 1px solid;
          background-color: transparent;
          margin-top: 40px; } } } } }

