.planner-window {
  background-color: rgba(#262220, .95);
  padding: 55px calc(20px + 10vw);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 5px;
  max-width: 90%;
  @media screen and ( max-width: 960px ) {
    &>form {
      max-width: 100%;
      &>.sides {
        flex-direction: column;
        &>div {
          margin-top: 2px; }
        &>div.input:nth-child(3) {
          &>input {
            padding-left: 20px;
            &:focus ~ label,
            &:not(:placeholder-shown) ~ label {
              left: -8px; } }
          &>label {
            left: 20px; } }

        &>div.sides-buttons {
          margin-top: -15px;
          min-width: 70px;
          justify-content: flex-end;
          top: 50%;

          &>button {
            margin-top: 0;
            &.button-reverce {
              width: 30px;
              height: 30px; }
            &.button-del {
              margin-left: 5px; } } } } } } }


.title {
  text-transform: uppercase;
  font-size: 18px;
  line-height: 30px;
  text-shadow: 0px 0px 20px rgba(0, 0, 0, 0.7);
  color: white;
  text-align: center;
  font-family: 'Merriweather', serif;
  letter-spacing: 0.08em;
  margin-bottom: 20px; }

.input {
  &-date {
    border: none;
    border-bottom: 1px solid #e2e2e2;
    text-align: center;
    display: block;
    background-color: transparent;
    color: white;
    margin: 5px auto 20px auto;
    padding: 5px; } }

.sides {
  position: relative;
  margin: 5px 0 10px;
  &>button {
    position: absolute; }
  &>button.button-reverce {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); } }

.distance-block {
  margin-top: -6px;
  display: flex;
  justify-content: space-around;
  &>p {
    font-size: 12px;
    font-weight: 300;
    color: white; }
  @media screen and ( max-width: 960px ) {
    flex-direction: column;
    &>p {
      text-align: center; }
    &>p:first-child {
      padding: 5px 0; } } }

.container {
  &-date {
    display: flex;
    justify-content: center;
    margin-bottom: 20px; } }

.calendarInput {
  &>div {
    &>input {
      padding: 10px;
      border-radius: 2px;
      border: 1px solid lighten(gray, 20); } } }

.calendarInputEmpty {
    &>div {
      &>input {
        padding: 10px;
        border-radius: 2px;
        border: 1px solid red; } } }
