.header {
  position: fixed;
  width: 100%;
  z-index: 3;
  max-width: 100%;
  &.lovered {
    background-color: rgba(black, .6); }
  &>nav {
    width: 90%;
    min-width: 350px;
    max-width: 1200px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    position: relative;
    @media screen and ( max-width: 500px ) {
      width: 100%;
      min-width: 350px;
      max-width: 100%; }
    &>.menu-icon, &>a:last-child {
      display: none; }

    &>.logo {
      max-width: 200px;
      height: 33px;
      &>img {
        max-width: 100%; } }
    &>ul {
      background-color: transparent;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      &>li {
        margin: 0 20px;
        &>a {
          display: block;
          text-decoration: none;
          letter-spacing: 1px;
          font-size: 14px;
          text-transform: uppercase;
          padding: 4px 0;
          color: #3C3835;
          position: relative;
          &::before {
            content: '';
            position: absolute;
            top: 2%;
            right: 1%;
            bottom: 2%;
            left: 1%;
            border-bottom: 2px solid #FF5C00;
            border-radius: 1px;
            transition: .5s;
            transform: scaleX(0); } }
        &:hover {
          &>a {
            &::before {
              transform: scale(1); } } } } }
    &>.menu-btn {
      display: none; } } }

.menu-icon {
  cursor: pointer;
  position: relative;
  user-select: none;
  width: 25px;
  height: 25px;
  &>span {
    display: block;
    width: 25px;
    height: 4px;
    background-color: black;
    position: absolute;
    border-radius: 2px; }
  &.white {
    &>span {
      background-color: white; } }
  &>span:first-child {
    top: 2px; }
  &>span:nth-child(2) {
    top: 10px; }
  &>span:nth-child(3) {
    top: 18px; } }

.tel {
  display: none; }
@media screen and ( max-width: 960px ) {
  .tel {
    width: 25px;
    height: 1px;
    display: block; }
  .header {
    &>a:last-child {
      color: white; }
    &>nav {
      &>.menu-btn {
        display: block;
        position: absolute;
        top: 12px;
        left: 20px;
        opacity: 0;
        width: 25px;
        height: 25px;
        z-index: 10;
        cursor: pointer; }
      &>.menu>li {
        margin: 0; }
      &>.menu-icon {
        display: block;
        &>span {
          z-index: 10; } }
      &>a:last-child {
        display: block; }
      &>.menu-btn:not(:checked) ~ .menu {
        transform: translateX(-100%);
        transition: all 1s ease; }
      &>.menu-btn:not(:checked) ~ .menu-icon>span {
        transition-delay: .5s;
        transition: all .5s ease; }
      &>.menu-btn:not(:checked) ~ a:last-child {
        transition-delay: .1s;
        transition: all .9s ease; }
      &>.menu-btn:checked ~ .menu-icon>span {
        background-color: white;
        transition-delay: .1s; }
      &>.menu-btn:checked ~ .menu-icon>span:first-child {
        transform: rotate(45deg);
        top: 10px;
        transition: all .5s ease-out; }
      &>.menu-btn:checked ~ .menu-icon>span:nth-child(2) {
        transform: scale(0);
        transition: all .5s ease-out; }
      &>.menu-btn:checked ~ .menu-icon>span:last-child {
        transform: rotate(-45deg);
        top: 10px;
        transition: all .5s ease-out; }
      &>.menu-btn:checked ~ a:last-child {
        color: white;
        z-index: 10;
        transition-delay: .5s; }
      &>.menu-btn ~ .logo {
        z-index: 10; } }



    .menu {
      position: absolute;
      top: 0;
      flex-direction: column;
      width: 105vw;
      margin-left: -9vw;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100vh;
      background-color: rgba(black, .9);
      transition: all 1s ease;
      &>li {
        padding: 4px 0;
        width: 100%;
        &>a {
          width: 100%;
          text-align: center;
          color: white;
          &::after {
            border-bottom: 1px solid grey;
            content: '';
            position: absolute;
            top: 2%;
            right: 1%;
            bottom: -5px;
            left: 1%;
            border-radius: 1px; }
          &::before {
            bottom: -5px; } } } } } }

