$global_radius: 4px;
$global_shadow: 2px 4px 4px rgba(0, 0, 0, 0.25);
$global_button_transition: all .2s ease-out;
$global_font_primary: 'Roboto', sans-serif;
$global_font_secondary: 'Merriweather', sans-serif;
$global_letter_spacing: 0.08em;
$global_title_font_color: #23211F;
$global_primary_color: #10BE00;
$global_secondary_color: #FF5C00;
$global_button_padding: 13px 22px;

@mixin defaults {
  * {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    outline: none;
    font-family: $global_font_primary;
    font-weight: 300; }
  html, body {
    max-width: 100%;
    overflow-x: hidden; }
  a {
    text-decoration: none;
    color: #000; }
  #root {
    width: 100%; }
  li {
    list-style: none; }
  input::-webkit-calendar-picker-indicator {
    display: none; } }


@mixin df($align, $justify) {
  display: flex;
  justify-content: $justify;
  align-items: $align; }

@mixin posa($top, $right, $bottom, $left) {
  position: absolute;
  top: $top;
  right: $right;
  bottom: $bottom;
  left: $left; }
@mixin background($image, $posY, $posX, $size) {
  background-image: url($image);
  background-position: $posY $posX;
  background-size: $size; }
@mixin widthRanges($width, $max, $min) {
  width: $width;
  max-width: $max;
  min-width: #{$min}; }
@mixin borderlines($height, $width) {
  content: '';
  height: $height;
  width: $width;
  border-left: 1px solid rgba(white, .2);
  border-right: 1px solid rgba(white, .2);
  transform: skewY(1.5deg);
  z-index: 1; }
@mixin button($primaryColor, $secondaryColor, $padding, $border) {
  background-color: $primaryColor;
  box-shadow: $global_shadow;
  border-radius: $global_radius;
  padding: $padding;
  color: $secondaryColor;
  cursor: pointer;
  font-weight: normal;
  border: $border solid $primaryColor;
  transition: $global_button_transition;
  z-index: 2;
  &:hover {
    background-color:  $secondaryColor;
    color: $primaryColor;
    border: $border solid $primaryColor; }
  &-o {
    background-color: $secondaryColor;
    color: $primaryColor;
    border: $border solid $primaryColor;
    z-index: 2;
    &:hover {
      background-color: $primaryColor;
      color: $secondaryColor;
      border: $border solid $primaryColor; } } }

@mixin title($padding, $fz, $lh,) {
  padding: $padding;
  font-family: $global_font_secondary;
  font-size: $fz;
  line-height: $lh;
  letter-spacing: $global_letter_spacing;
  color:  $global_title_font_color;
  text-transform: uppercase;
  text-align: center; }

@mixin modals() {
  position: fixed;
  width: 100%;
  height: 100%; }

