.checkbox {
  display: flex;
  position: relative;
  padding-left: 40px;
  margin-bottom: 20px;
  cursor: pointer;
  user-select: none;
  font-size: 14px;
  text-align: left;
  &:last-child {
    margin-bottom: 0; }
  &>input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0; }
  &>span {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    border: 2px solid #757575;
    border-radius: 4px; }
  &:hover input ~ span {
    border-color: darken(#5200ff, 20); }
  & input:checked ~ span {
    border-color: #5200ff; }
  & span:after {
    content: "";
    position: absolute;
    display: none; }
  & input:checked ~ span:after {
    display: block; }
  & span:after {
    left: 5px;
    top: 3px;
    width: 3px;
    height: 7px;
    border: solid #5200ff;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg); } }
