.notfound {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  padding: 5%;
  &>h2 {
    font-size: 3em;
    font-weight: normal;
    transform: translateY(-100%);
    text-align: center; }
  &>p {
    transform: translateY(-200%);
    margin: 5px;
    text-align: center;
    @media screen and ( max-width: 960px ) {
      transform: translateY(-50px);
      margin: 2px; } } }
