.input {
  display: flex;
  position: relative;
  background-color: white;
  min-width: 350px;
  height: fit-content;
  border: none;
  border-radius: 3px;
  @media screen and ( max-width: 960px  ) {
    min-width: 100%; }
  &:nth-child(2) {
    &>input {
      padding-left: 40px; }
    &>label {
      left: 40px; } }
  &>input {
    padding: 20px 30px 15px 35px;
    width: 100%;
    background-color: transparent;
    border: none;
    &::placeholder {
      color: transparent; }
    &:focus ~ label,
    &:not(:placeholder-shown) ~ label {
      transform: scale(0.7);
      top: 3px;
      left: 5px;
      transition: all .3s ease-out; } }
  &>label {
    position: absolute;
    left: 40px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 0.8em;
    background-color: transparent;
    transition: all .3s ease-out;
    cursor: text; } }
div.input {
  &:nth-child(3) {
    margin-left: -5px;
    @media screen and ( max-width: 960px ) {
      margin-left: 0; } }
  &:first-child {
    &>label {
      left: 20px; }
    &>input {
      padding: 20px 30px 15px 25px; }
    &>input:focus ~ label,
    &>input:not(:placeholder-shown) ~ label {
      left: -10px; } } }
