.calculator-select {
  width: 100%;
  &--item {
    border-radius: 5px;
    &:hover {}
    &>div {
      &>div:first-child {
        &>div:first-child {} } } }
  &--disabled {
    opacity: .8;

    &>div:last-child {} } }
.input-send-to {
  margin-left: 10px;
  @media screen and ( max-width: 960px ) {
    margin-left: 0; }
  &>div {
    &>div {
      &>div:first-child {
        @media screen and ( max-width: 960px  ) {
          padding-left: 0; } } } } }

