.driver {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.25);
  border-radius: 3px;
  padding: 30px 40px;
  &:first-child {
    margin-top: 0; }
  &>figure {
    width: 35%;
    max-height: 240px;
    display: flex;
    justify-content: center;
    align-items: center;
    &>img {
      margin-top: -20%; }
    @media screen and ( max-width: 780px ) {
      width: 80%; }
    @media screen and ( max-width: 600px ) {
      width: 100%; } }
  &_info {
    display: flex;
    flex-direction: column;
    width: 60%;
    min-height: 240px;
    &_driver {
      display: flex;
      &:hover {
        &>.driver_info_driver-data>div>.driver_info_driver-data_name {
          color: darken(#ff5c00, 20); } }
      &>figure {
        width: 130px;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100px;
        border-radius: 5px;
        overflow: hidden;
        &>img {
          height: 140%; } }
      @media screen and ( max-width: 780px ) {
        justify-content: flex-start; }
      &-car {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        min-height: 10px;
        max-height: 45px;
        @media screen and ( max-width: 780px ) {
          display: none; } }
      &-data {
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin-left: 20px;
        &_name {
          font-size: 18px;
          font-weight: 400; }
        &_rating {
          margin-top: 10px;
          &>object {
            margin-left: 7px;
            &:first-child {
              margin: 0; } } }
        &_reviews {
          margin-top: 10px;
          color: #493f3a;
          font-size: 12px;
          font-weight: 300; }
        @media screen and ( max-width: 780px ) {
          margin-left: 20px; } } }
    &_car {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      &_name {
        margin-top: 15px; }
      &-text {
        ul {
          margin-top: 9px;
          li {
            margin-top: 10px;
            &:first-child {
              margin-top: 0; } } }
        @media screen and ( max-width: 500px ) {
          width: 100%; }
        &_title {
          display: inline-block;
          font-size: 14px;
          width: 170px;
          font-weight: 400;
          @media screen and ( max-width: 500px ) {
            width: 150px; } }
        &_description {
          font-size: 12px;
          width: 120px; } }
      &-button {
        padding: 10px 40px;
        @media screen and ( max-width: 500px ) {
          margin-top: 35px; } }
      @media screen and ( max-width: 500px ) {
        display: flex;
        flex-direction: column;
        align-items: center; } }
    @media screen and ( max-width: 780px ) {
      margin-top: 40px;
      width: 80%; }
    @media screen and ( max-width: 600px ) {
      width: 100%; } }
  @media screen and ( max-width:  1361px) {
    width: 100%; }
  @media screen and ( max-width: 780px ) {
    flex-direction: column;
    align-items: center; } }


