.tours-slider {
  margin-bottom: 50px;
  max-width: 950px;
  @media screen and ( max-width: 960px ) {
    max-width: 760px; }
  @media screen and ( max-width: 690px ) {
    max-width: 350px; }
  &>.tours-title {
    font-size: 24px;
    line-height: 28px;
    &>img {
      width: 16px;
      height: 19px;
      margin-right: 10px; } }
  &>.slider-one {
    display: flex;
    height: 330px;
    position: relative;
    max-width: 100%;
    @media screen and ( max-width: 500px ) {
      margin-left: calc(5% + 10px); }
    &>.slider-one-wrapper {
      display: flex;
      position: absolute;
      transition: all .5s ease-out;
      &>div {
        flex: 1;
        opacity: 1;
        transform: scale(1);
        transition: transform .5s ease-out, opacity .3s ease;
        margin-right: 20px; } }
    &>div {
      margin-left: 10px;
      flex: 1; }
    &>.controllers {
      position: absolute;
      bottom: 0;
      right: 0;
      z-index:  1;
      @media screen and ( max-width: 500px ) {
        right: 55%;
        transform: translate(40%, 120%); }
      &>.buttons {
        display: flex;
        justify-content: space-between;
        &>button {
          display: block;
          background-color: #1A6D12;
          border: 1px solid #1A6D12;
          width: 23px;
          height: 23px;
          color: white;
          position: relative;
          cursor: pointer;
          &::before {
            content: '';
            width: 8px;
            height: 2px;
            background-color: white;
            position: absolute;
            top: 6px;
            left: 6px;
            transform: rotate(50deg);
            border-radius: 2px; }
          &::after {
            content: '';
            width: 8px;
            height: 2px;
            background-color: white;
            position: absolute;
            top: 12px;
            left: 6px;
            transform: rotate(-50deg);
            border-radius: 2px; }
          &:first-child {
            &::before {
              transform: rotate(-50deg); }
            &::after {
              transform: rotate(50deg); } }
          &:hover {
            background-color: transparent;
            border: 1px solid #1A6D12;
            &::before, &:after {
              background-color: #1A6D12; } } } }

      &>.scroller {
        display: flex;
        justify-content: space-between;
        min-width: 60px;
        border-bottom: 1px solid #1A6D12;
        position: relative;
        padding: 5px 0;
        margin-bottom: 5px;
        &>div {
          font-size: 0.7em; }
        &>span {
          display: block;
          position: absolute;
          left: 0;
          width: 25%;
          height: 5px;
          background-color: #1A6D12;
          bottom: 0;
          transform: translate( 15% ,60%); } } } } }
@mixin slider($cardsArrayLength, $visibleCards ) {
  @for $i from 0 to $cardsArrayLength {
    div.active-slide-#{$i} {
      &>.slider-one-wrapper {
        @for $j from 0 to $cardsArrayLength {
          @if $j < $i {
            &>.slide-#{$j} {
              transform: scale(.9);
              opacity: 0.6;
              transition: transform .5s ease-out, opacity .3s ease; } }
          @else if $j > $i + $visibleCards {
            &>.slide-#{$j} {
              transform: scale(0);
              transition: transform .5s ease-out, opacity .3s ease; } } } } } } }

@include slider( 8, 2 );

@media screen and ( max-width: 960px ) {
  @include slider(8, 1); }
@media screen and ( max-width: 690px ) {
  @include slider(8, 0); }
@media screen and ( max-width: 500px ) {
  @for $i from 0 to 8 {
    div.active-slide-#{$i} {
      &>.slider-one-wrapper {
        @for $j from 0 to 8 {
          @if $j < $i {
            &>.slide-#{$j} {
              transform: scale(.9);
              opacity: 0.6;
              transition: transform .5s ease-out, opacity .3s ease; } }
          @else if $j == $i + 1 {
            &>.slide-#{$j} {
              transform: scale(.9);
              opacity: 0.6; } }
          @else if $j > $i + 1 {
            &>.slide-#{$j} {
              transform: scale(0); } } } } } } }

.slide-disabled {
  opacity: .3;
  &:hover {
    background-color: green !important; }
  &::before, &:after {
    background-color: white !important; } }

