.banner {
  background: linear-gradient( rgba(48, 46, 43, 0.8), rgba(48, 46, 43, 0.8) ), url('../../pages/aboutPage/mainbanner.jpg') center (center / cover) no-repeat;
  position: relative;
  &_description {
    color: white;
    text-align: center;
    &>div.about-link {
      position: absolute;
      bottom: 40px;
      left: 50%;
      transform: translateX(-50%);
      width: auto;
      height: fit-content; }
    &_title {
      font-family: Merriweather, sans-serif;
      font-weight: bold;
      font-size: 36px; }
    &_subtitle {
      color: #c8c8c8;
      font-size: 10px;
      margin-top: 10px; }
    &_text {
      font-size: 18px;
      font-weight: 400;
      margin-top: 30px;
      @media screen and ( max-width: 500px ) {
        font-size: 14px;
        line-height: 1.5em; } } }
  &_scroll {
    transition: 0.2s;
    &:hover {
      fill: #ff5c00;
      cursor: pointer; } } }


.wrapper {
  display: flex;
  justify-content: center;
  padding-bottom: 60px; }
.nature {
  max-width: 1360px;
  padding: 20px 10% 60px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  &:first-child {
    margin-top: 40px; }
  &_info {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 90px;
    max-width: 50%;
    &_title {
      color: #000000;
      font-size: 18px;
      font-family: Merriweather, sans-serif;
      text-align: center; }
    &_text {
      margin-top: 25px;
      color: #000000;
      font-size: 16px;
      font-weight: 300;
      line-height: 22px; }
    &>a {
      display: inline-block;
      margin-top: 20px; } } }


@media screen and (max-width: 1200px) {
  .nature {
    &_info {
      margin-left: 70px;
      &_title {
        margin-top: 40px;
        font-size: 16px; }
      &_text {
        margin-top: 22px;
        font-size: 14px;
        line-height: 20px; }
      &>button {
        margin: 20px auto;
        font-size: 14px; } } } }


@media screen and (max-width: 960px) {
  .nature {
    flex-direction: column;
    &_info {
      max-width: 100%;
      margin: 0;
      &_title {
        text-align: center; }
      &>button {
        margin: 20px auto;
        display: block;
        text-transform: uppercase; } } } }

