.profits {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 77px 10%;
  @media screen and ( max-width: 1393px ) {
    &>.profit {
      &:nth-last-child(2) {
        border: none; }
      &:last-child {
        margin-top: 50px;
        display: none; } } }
  @media screen and ( max-width: 1044px ) {
    &>.profit {
      &:nth-last-child(2) {
        margin-top: 50px;
        border-right: 1px solid #E4E4E4; }
      &:nth-last-child(3) {
        border: none; }
      &:last-child {
        margin-top: 50px;
        display: flex; } } }
  @media screen and ( max-width: 696px ) {
    padding: 30px 5%;
    &>.profit {
      &:nth-child(n+1) {
        border: none;
        max-width: 50%;
        min-width: 150px;
        margin-top: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: 5px;
        &>.profit-text {
          padding: 30px 15px; }
        &>.profit-icon {
          width: 60px;
          height: 60px;
          padding: 10px;
          display: flex;
          justify-content: center;
          align-items: center;
          &>img {
            width: 60%;
            height: 60%; } } } } }
  @media screen and ( max-width: 480px ) {
    &>.profit {
      &:nth-child(n+1) {
        border: none;
        max-width: 80%;
        min-width: 150px;
        margin-top: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: 5px;
        &>.profit-text {
          padding: 30px 15px; }
        &>.profit-icon {
          width: 60px;
          height: 60px;
          padding: 10px;
          display: flex;
          justify-content: center;
          align-items: center;
          &>img {
            width: 60%;
            height: 60%; } } } } }
  &>.profit {
    max-width: 25%;
    min-width: 280px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    border-right: 1px solid #E4E4E4;
    &:last-child {
      border: none; }
    &>.profit-icon {
      width: 120px;
      height: 120px;
      padding: 40px;
      border-radius: 50%;
      box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
      border: 1px solid #DCDCDC;
      &>img {
        width: 100%;
        height: 100%; } }
    &>.profit-text {
      text-align: center;
      padding: 30px 30px; } } }

.transfer {
  &-about {
    padding: 0 10%; }
  &-heading {
    text-align: center;
    font-family: Merriweather;
    font-size: 24px;
    line-height: 150%;
    text-transform: uppercase;
    margin-bottom: 20px;
    letter-spacing: 0.05em; }
  &-text {
    line-height: 150%;
    font-size: 15px;
    margin-bottom: 50px; } }
