.about-slider {
    position: relative;
    width: 40%;
    height: fit-content;
    &-slide {
      width: 100%;
      height: 100%;
      transition: all 1s ease-out;
      z-index: 2;
      &>img {
        box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 10px;
        width: 100%;
        height: 100%; }
      &:first-child {
        width: 100%;
        box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 10px;
        height: 285px;
        background-color: grey;
        @media screen and ( max-width: 540px ) {
          height: 180px; } }
      &:nth-child(2) {
        position: absolute;
        top: 0;
        left: 50%;
        transform: scale(0.25) translate(-20%, -130% );
        transition: all .5s ease-out; }
      &:nth-child(3) {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: scale(0.25) translate(-20%, 0% ); }
      &:nth-child(4) {
        position: absolute;
        bottom: 0;
        left: 50%;
        z-index: 2;
        transform: scale(0.25) translate(-20%, 130% ); } }

    @media screen and ( max-width: 960px ) {
      width: 50vw;
      &>img {
        &:nth-child(2) {
          left: 0;
          transform: translateX(-20%) translateY(-20%); }
        &:nth-child(3) {
          transform: translateX(20%) translateY(20%); } } } }


@for $i from 1 to 5 {
  .about-slider.active-slide-#{$i} {
    .slide-#{$i} {
      transform: scale(1) translateX(-50%);
      transition: all 1s ease-out;
      z-index: 1; } } }
