.sides {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  background-color: #fff;
  border-radius: 5px;
  &>input {
    padding: 10px;
    &:first-child {
      padding-right: 20px; }
    &:nth-child(2) {
      padding-left: 30px; } }
  @media screen and ( max-width: 960px ) {
    &>.input {
      width: 100%; } } }

.input-send-from-empty {
  border: 1px red solid;
  border-radius: 5px; }

.button-reverce {
  background-color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  box-shadow: 2px 4px 8px rgba(0, 0, 0, 0.35);
  cursor: pointer;
  &:hover {
    box-shadow: 2px 4px 8px rgba(0, 0, 0, 0.6); }
  &>img {
    width: 60%;
    height: 60%; } }


.sides-buttons {
  position: absolute;
  top: 0px;
  right: 0px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 380px;
  height: 1px;
  transform: scale(1.2);
  &>.button-reverce {
    z-index: 1;
    margin-left: 55px;
    margin-top: -3px;
    @media screen and ( max-width: 1060px  ) {
      margin-left: 125px; }
    @media screen and ( max-width: 960px  ) {
      position: absolute;
      right: 10px; } }

  &>.button-del {
    padding: 5px;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    background-color: white;
    border: none;
    transform: none;
    box-shadow: 2px 4px 8px rgba(0, 0, 0, 0.40);
    transition: all .5s;
    cursor: pointer;
    position: relative;
    z-index: 3;
    margin-top: 2px;
    margin-right: 5px;
    &:hover {
      box-shadow: 2px 4px 8px rgba(0, 0, 0, 0.7);
      transition: all .5s;
      &::after,
      &::before {
        background-color: black; } }
    &::after {
      content: '';
      position: absolute;
      top: 14px;
      left: 7px;
      height: 2px;
      width: 16px;
      background-color: lighten(black, 30% );
      transform: rotate(45deg); }
    &::before {
      content: '';
      position: absolute;
      top: 14px;
      left: 7px;
      height: 2px;
      width: 16px;
      background-color: lighten(black, 30%  );
      transform: rotate(-45deg); } } }
