.contacts-wrapper {
  padding-top: 5%;
  &>h1.page-title {
    padding-top: 100px;
    padding-bottom: 20px; }
  &>h2.page-subtitle {
    font-size: 24px;
    font-family: Merriweather, sans-serif;
    text-align: center; } }
.contacts {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  padding: 0 5%;
  max-width: 1121px;
  margin: auto;
  min-height: 70vh;

  &>div:nth-child(1) {
    width: 40%;
    min-width: 320px;
    max-width: 480px;
    padding: 50px 0; }
  &>div:nth-child(2) {
    flex-shrink: 2;
    max-width: 100px;
    min-height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 5%; }
  &>form {
    padding: 50px 20px;
    min-width: 320px;
    max-width: 480px;
    width: 40%;
    min-height: 300px;
    &>.input {
      width: 80%;
      min-width: 320px;
      &:nth-child(2) {
        margin: 0; }
      &:nth-child(3) {
        margin: 0; }
      &>label {
        left: 5px;
        margin: 0; }
      &>input {
        padding-left: 5px; }
      &.contacts-email>input:focus ~ label,
      &.contacts-email>input:not(:placeholder-shown) ~ label {
        margin-left: -14px; }
      &.contacts-name>input:focus ~ label,
      &.contacts-name>input:not(:placeholder-shown) ~ label {
        margin-left: -5px; } } }
  &-list {
    &-title {
      font-size: 16px;
      font-weight: 500;
      margin-bottom: 30px; }
    &>ul {
      &>li {
        margin-bottom: 1em;
        &>span {
          font-size: 14px;
          &:first-child {
            font-weight: 600;
            min-width: 90px;
            display: inline-block; } } } } }
  @media screen and ( max-width: 960px ) {
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 50px 0;
    &>div:nth-child(2) {
      display: none; }
    &>div:first-child {
      padding: 0;
      &>h3 {
        text-align: center; }
      &>ul>li {
        margin: 1em auto; } }
    &>form {
      padding: 40px 0;
      &>h3 {
        text-align: center; }
      &>.input {
        min-width: 100%; } } } }

