footer {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  width: 100vw;
  height: 1px;
  background: transparent;
  bottom: 0;
  &>div {
    width: 20%;
    min-width: 200px;
    max-width: 300px;
    text-align: center;
    transform: translateY(-100%);
    &>img {
      max-height: 100%; } }
  @media screen and ( max-width: 760px ) {
    flex-direction: column;
    bottom: 25px;
    &>div {
      margin: 5px; }
    &>div:first-child {
      order: 3; } } }
