* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

figure {
  border-radius: 5px;
  overflow: hidden;
  &>img {
    min-height: 100%;
    border-radius: 5px; } }

.container {
  position: relative;
  min-height: calc(100vh - 85px);
  width: 100%;
  padding: 0 0 20px 0;
  &>div.wrapper {
    margin: auto; }
  &.main-page {
    @include df(center, center);
    @include background('./mainpagebg.jpg', center, center, cover);
    position: relative;
    min-height: 100vh;
    padding-top: 0;
    flex-direction: column;
    overflow-y: auto;
    color: white;
    &::after {
      @include posa(20%, 10%, 20%, 10%);
      @include borderlines(60%, 80%);
      @media screen and ( max-width: 760px ) {
        display: none; } }
    &::before {
      @include posa(20%, 30%, 20%, 25%);
      @include borderlines(60%, 45%);
      @media screen and ( max-width: 760px ) {
        display: none; } } } }

.button-group {
  @include df(center, space-between);
  @include widthRanges(75%, 700px, 100%);
  padding: 10px 0;
  margin: auto;
  @media screen and ( max-width: 1060px ) {
    min-width: 100%;
    justify-content: center; }

  &.page {
    margin-bottom: 80px;
    max-width: 600px;
    min-width: 600px;
    @media screen and ( max-width: 960px ) {
      min-width: 100%;
      &>div {
        width: 100%;
        @include df(center, center);
        margin-bottom: 20px; } } }
  @media screen and ( max-width: 1060px ) {
    min-width: 100%;
    flex-wrap: wrap;
    &>button {
      min-width: 50px;
      width: 100%;
      padding: 10px 15px; }
    &>button:nth-child(2) {
      order: 3;
      width: 100%;
      margin-top: 10px; }
    &>button:nth-child(1) {
      width: 49%;
      padding: 10px 5px; }
    &>button:nth-child(3) {
      width: 49%;
      padding: 10px 5px; } } }


.button {
  @include button($global_primary_color, white, $global_button_padding, 2px);
  &:hover {
    background-color: transparent;
    color: white; } }



.button-orange {
  @include button($global_secondary_color, white, $global_button_padding, 1px);
  min-width: 170px;
  text-align: center; }

.button-gray {
  @include button(gray, white, gray, 1px);
  min-width: 170px;
  text-align: center;
  padding: 13px 35px;
  margin-right: 20px;
  transition: .3s all;
  font-size: 14px;
  background: transparent;
  border: 1px solid gray;
  color: gray;
  box-shadow: none;
  &:hover {
    background-color: gray;
    color: white; } }

.buttons-planner {
  background-color: $global_secondary_color;
  box-shadow: $global_shadow;
  border-radius: $global_radius;
  padding: 13px;
  min-width: 200px;
  color: white;
  cursor: pointer;
  font-weight: normal;
  border: 1px solid $global_secondary_color;
  transition: $global_button_transition;
  margin-right: 20px;
  @media screen and ( max-width: 1060px ) {
    margin: 0; }
  &:last-child {
    margin-right: 0; }
  &:hover {
    background-color: transparent;
    color: white;
    border: 1px solid $global_secondary_color; }
  &-o {
    background-color: transparent;
    color: white;
    border: 1px solid white;
    &:hover {
      background-color: transparent;
      color: $global_secondary_color;
      border: 1px solid $global_secondary_color; } } }

h1.page-title {
  @include title(5vh, 26px, 40px); }

.modal {
  @include modals();
  @include df(flex-start, center);
  top: 0;
  left: 0;
  z-index: 5;
  &-background {
    @include posa(0,0,0,0);
    background-color: rgba(black, .7);
    z-index: 3; }
  &-text {
    background-color: white;
    border-radius: 15px;
    z-index: 5;
    margin-top: 10%;
    @media screen and ( max-width: 780px ) {
      width: 70%;
      @media screen and ( max-width: 620px ) {
        width: 100%; } }
    &>form {
      @include df(flex-start, center);
      flex-wrap: wrap;
      position: relative;
      width: 400px;
      @media screen and ( max-width: 600px ) {
        width:  270px; }
      &>.form_title {
        width: 100%;
        font-size: 14px;
        text-align: center; }
      &>.form_send {
        display: block;
        margin: 0 0; }
      &>.close {
        position: absolute;
        top: -30%;
        right: -25%;
        border: none;
        background-color: transparent;
        font-size: 40px;
        color: white;
        cursor: pointer;
        @media screen and ( max-width: 780px ) {
          top: -20%;
          right: -15%; }
        @media screen and ( max-width: 600px ) {
          top: 0;
          right: 0;
          color: gray; }
        &:hover {
          color: darken(#FDF6E4, 20); } }
      &>.form_inputs {
        @include df(flex-start, center);
        flex-wrap: wrap;
        width: 100%;
        margin-top: 20px;
        margin-bottom: 40px;
        &>.checkbox {
          margin-top: 20px; }
        &>.input {
          width: 45%;
          min-width: 150px;
          @media screen and ( max-width: 600px ) {
            width: 100%; }
          &_long {
            width: 100%; }
          &>input {
            width: 100%;
            border-bottom: 1px solid gray; }
          &>label {
            left: 20px; } } } } } }

.testpage {
  display: flex;
  justify-content: center;
  padding-top: 200px; }
