.tours-slider-slide {
  width: 250px;
  height: 300px;
  position: relative;
  &>figure {
    width: 100%;
    height: 100%;
    margin: 0;
    overflow: hidden;
    border-radius: 5px;
    margin: 30px 0;
    &>img {
      display: block;
      height: 103%;
      margin: 0 -40%; } }
  &-text {
    position: absolute;
    bottom: -30px;
    right: 0;
    background-color: rgba(black, 0.2);
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-direction: column;
    padding: 10px;
    &-title {
      font-weight: bold;
      font-size: 13px;
      color: #FFFFFF;
      border-bottom: 1px solid white;
      padding: 12px 0; }
    &-time {
      font-size: 12px;
      color: white;
      padding: 10px 0 0 15px;
      position: relative;
      &>img {
        height: 12px;
        width: 12px;
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(-20%, 90%); } } } }


