.inputContainer {
  position: relative;
  &-content {
    display: flex;
    &>select {
      height: 35px;
      margin-right: 20px;
      border-radius: 5px; } }
  &>div>input {
    background: #FFFFFF;
    border: 1px solid lighten(gray, 30);
    box-sizing: border-box;
    border-radius: 8px;
    height: 35px;
    margin-bottom: 30px;
    padding-left: 50px;
    transition: all .5s;
    &:hover {
      box-shadow: 0px 4px 12px rgba(46, 0, 255, 0.1);
      border: 1px solid lighten(gray, 20); }
    &:focus {
      box-shadow: 0px 4px 12px rgba(46, 0, 255, 0.1);
      border: 1px solid lighten(gray, 20); }
    &.inputContainer-true {
      border: 1px solid #BFEFB7; }
    &.inputContainer-error {
      border: 1px solid #f07a7a; } }
  &>figure {
    position: absolute;
    left: 12px;
    top: 9px; } }
