.main {
  &-title {
    font-size: 54px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 60px;
    text-align: center;
    text-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
    &::before {
      content: '';
      position: absolute;
      top: 20%;
      bottom: 0;
      left: 0;
      right: 0;
      width: 50%;
      height: 60%;
      border-right: 1px solid rgba(white, .2);
      z-index: 1;
      @media screen and ( max-width: 760px ) {
        display: none; } } }
  &-subtitle {
    font-size: 20px;
    transform: translateX(75%);
    margin-bottom: 70px;
    z-index: 2;
    &::before {
      content: '';
      position: absolute;
      width: 50px;
      height: 50%;
      top: 0;
      left: 0;
      transform: translateX(-110%);
      border-bottom: 1px solid white; } } }
@media screen and ( max-width: 500px ) {
  .main {
    &-title {
      font-size: 30px;
      line-height: 45px; }
    &-subtitle {
      font-size: 14px;
      transform: translateX(50%);
      &::before {
        width: 40px;
        border-bottom: 1px solid white; } } } }

