.wrapper {
  display: flex;
  justify-content: center; }

.wrapper_container {
  width: 1360px;
  padding-bottom: 40px;
  margin: auto;
  @media screen and ( max-width:  1361px) {
    width: 95%; } }

.rout {
  &_h3 {
    margin-top: 45px;
    margin-bottom: 35px;
    font-family: Merriweather;
    font-size: 24px;
    text-align: center; } }

.sort {
  display: flex;
  margin-bottom: 20px;
  width: 100%;
  max-width: 700px;
  align-items: center;
  &_title {
    color: #2c2a24;
    font-size: 18px;
    @media screen and ( max-width: 780px ) {
      display: none; } }
  &_items {
    display: flex;
    justify-content: space-around;
    margin-left: 50px;
    width: 100%;
    @media screen and ( max-width: 780px ) {
      margin-left: 0; }
    &>li {
      color: darken(blue, 15%);
      font-size: 14px;
      font-weight: 300;
      cursor: pointer;
      min-height: 20px;
      @media screen and ( max-width: 780px ) {
        font-size: 12px; }
      &:hover {
        border-bottom: 1px solid darken(blue, 15%); }
      &.active {
        color: black;
        &:hover {
          border: none; } } } } }


.select_driver {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  width: 75%;
  @media screen and ( max-width:  1361px) {
    margin: auto;
    width: 100%; } }


.market_drivers {
  display: flex;
  @media screen and ( max-width:  1361px) {
    flex-direction: column-reverse; } }

.drivers {
  width: 95%;
  min-height: 700px;
  @media screen and ( max-width:  1361px) {
    width: 100%; } }


.filter {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  box-shadow: 0 4px 9px rgba(0, 0, 0, 0.25);
  border-radius: 1px;
  padding: 36px 22px;
  width: 25%;
  &_invision {
    background-color: transparent;
    width: 100%;
    display: none; }
  &_h3 {
    font-family: Merriweather;
    font-size: 18px;
    margin-bottom: 35px;
    z-index: -1;
    user-select: none;
    @media screen and ( max-width: 1024px ) {
        margin-bottom: 0; } }
  &_h4 {
    font-family: Merriweather;
    font-size: 12px;
    letter-spacing: 0.6px;
    text-transform: uppercase;
    margin-bottom: 20px;
    text-align: center; }
  &_done {
    position: absolute;
    transform: translateX(-150px) translateY(2px);
    display: none;
    height: 0;
    cursor: pointer;
    &::after {
      z-index: 3;
      content: "▼";
      font-size: 16px;
      color: lighten(black, 50);
      background-color: rgba(0, 0, 0, 0);
      padding: 35px 130px 30px 200px; }
    @media screen and ( max-width: 1024px ) {
      display: block; }
    &:checked::after {
      content: "▲"; }
    &:checked ~ .filter_container {
      transform: scale(1);
      max-height: 800px;
      opacity: 1;
      visibility: visible;
      transition: all .5s linear; }
    & ~ .filter_invision {
      @media screen and ( max-width: 1024px ) {
        display: block;
        min-height: 0;
        transition: all .5s linear; } }
    &:checked ~ .filter_invision {
      @media screen and ( max-width: 1024px ) {
        min-height: 35px;
        transition: all .5s linear; } } }
  &_addition {
    &>.filter_h4 {
      margin-top: 30px;
      @media screen and ( max-width: 1361px )  and ( min-width: 1024px ) {
        text-align: left; } } }
  &_fuel {
    display: flex;
    flex-direction: column;
    width: 100%;
    &>.filter_h4 {
      @media screen and ( max-width: 1361px )  and ( min-width: 1024px ) {
        text-align: left; } } }
  &_sits {
    width: 100%;
    margin-top: 40px;
    @media screen and ( max-width: 1361px ) and ( min-width: 1024px ) {
      margin-top: 60px; }
    &>select {
      border: 1px solid #eee;
      border-radius: 5px;
      padding-left: 15px;
      width: 280px;
      height: 40px; } }
  &_price-inputs {
    display: flex;
    align-items: center; }
  &_price>div {
    width: 100%;
    max-width: 280px; }
  &_cars {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    min-height: 180px;
    width: 280px;
    &_container {
      display: flex;
      flex-direction: column-reverse;
      justify-content: space-around;
      text-align: center;
      position: relative;
      cursor: pointer;
      padding: 10px;
      user-select: none;
      height: 70px;
      width: 130px;
      &>input {
        position: absolute;
        opacity: 0;
        height: 0;
        width: 0; }
      &_checkmark {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-color: #dee9ec;
        border-radius: 3px;
        z-index: -1; }
      &>img {
        z-index: 1; }
      &:hover input ~ span {
        background-color: lighten(#dee9ec, 2); }
      &>input:checked ~ span {
        border: 2px solid #5200ff; }
      &>span:after {
        content: '';
        position: absolute;
        display: none; }
      &>input:checked ~ span:after {
        display: block; }
      &>span:after {
        position: absolute;
        left: 114px;
        bottom: 9px;
        height: 100%;
        width: 100%;
        background: url('../../components/driverFilter/checked.svg')no-repeat; } } }
  &_container {
    width: 100%;
    @media screen and ( max-width: 1024px ) {
      opacity: 0;
      transform: scale(0.98);
      visibility: hidden;
      transition: all .5s linear;
      max-height: 0; }
    &_block {
      min-width: 280px;
      margin-top: 40px;
      &:first-child {
        margin-top: 0; }
      @media screen and ( max-width: 1361px )  and ( min-width: 1024px ) {
        margin-top: 0;
        &:first-child {
          order: 2; }
        &:nth-child(2) {
          order: 1; }
        &:last-child {
          order: 3; } } }
    @media screen and ( max-width: 1361px ) {
      display: flex;
      flex-direction: row;
      justify-content: space-between; }
    @media screen and ( max-width: 1024px ) {
      flex-direction: column;
      justify-content: flex-start;
      align-items: center; } }
  @media screen and ( max-width:  1361px) {
    margin: auto;
    width: 100%;
    margin-bottom: 50px; } }
